.user {
    width: 100%;
    height: 100%;
    display: flex;
    // text-align: center;
    color: #000;
    box-sizing: border-box;
    padding: 20px;
    background: #fff;
}

.userleft {
    width: 20%;
    height: 100%;
    border: 1px solid #ccc;

}

.userleft>ul {
    height: 100%;
    width: 100%;
}

.userleft>ul>li:nth-child(1) {
    border-bottom: 1px solid #ccc;
    ;
}

.userleft>ul>li {
    border-bottom: 1px solid #ccc;
    box-sizing: border-box;
}

.userleft>ul>li:nth-child(2) {
    margin-top: 50px;
    border-bottom: 0;
}

.userleft>ul>li>p {
    line-height: 60px;
    text-align: center;
    height: 60px;
}

.userleft>ul>li>p.active {
    background: #777;
}

.userleft>ul>li>p:hover {
    background: #ccc;
}

.userright {
    width: 80%;
    height: 100%;
    border-right: 1px solid #ccc;
    border-top: 1px solid #ccc;
    display: flex;
    flex-direction: column;
}

.userright>ul {
    height: 100%;
}

.userright>ul>li {
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;

}

.userright>ul>li:nth-child(2) {
    box-sizing: border-box;
    padding: 10px
}

.userright>ul>li:nth-child(4) {
    height: 15.5%;
    display: flex;
    align-items: center;
}

.userright>ul>li:nth-child(4)>button {
    margin: 10px
}

.search {
    height: 30px;
    border: 1px solid #ccc;

}

.find {
    margin: 0 20px;
}

.userright #userLi {
    position: relative;
    box-sizing: border-box;
    padding: 10px 10px 0 10px;
    height: 75%;
}
table {

    margin: auto;
    width: 100%;
}

tr {
    height: 30px;
    text-align: center;
}

tr:nth-child(2n+1) {
    background: #eee;
}

td {
    border: 1px solid #ccc;
}

td>span:nth-child(1) {
    padding: 0 10px;
    color: skyblue;
}

td>span:nth-child(2) {
    border-left: 1px solid #ccc;
    padding: 0 10px;
    color: skyblue;

}

.page {
    margin-left: 200px;
}

.addStaff {
    width: 100%;
    line-height: 40px;
}

.addStaff>li {
    height: 40px;
    margin: 10px 0;
    display: flex;
}
.addStaff>li:nth-child(9){
   display: flex;
   justify-content: center;
}
.addStaff>li>span {
    display: block;
    width: 60px;
}

.addStaff>li>input {
    outline: none;
    border: 1px solid #000;
    height: 100%;
    width: 80%;
}
